import * as React from 'react';
// import { Chart } from 'react-charts';

import Staff, { HitWindowSearchField } from '@miq/staffjs';
import { Icons } from '@miq/componentjs';
import { SharedDataCtx } from '@miq/contextjs';

import { getImgUrl } from '@miq/utiljs';
import { useDocumentTitle } from '@miq/hookjs';

//

const PerformanceCard = ({ settingSlug, ...props }: any) => {
  const ctx = React.useContext(SharedDataCtx);
  const { stats } = ctx;
  if (!stats) return null;

  return (
    <Staff.Section
      title="Insights"
      actions={
        <div className="d-flex align-items-center">
          <HitWindowSearchField options={['today', 'last_7', 'last_30']} />
          <Staff.Link to="insights" className="btn btn-primary-3 ms-1">
            {`Insights>`}
          </Staff.Link>
        </div>
      }
    >
      <div className="d-grid gap-2 mb-3">
        <div className="">Messages: {stats?.msg_count}</div>
        <div className="">Visitors: {stats?.visitor_count}</div>
        <div className="">Page views: {stats?.views_count}</div>
      </div>
    </Staff.Section>
  );
};

export default function ShopStaffIndexView() {
  useDocumentTitle('Store');
  const ctx = React.useContext(SharedDataCtx);
  const { isLoaded, products, orders, cats, shopy_settings, site = {}, stats } = ctx;
  const settingSlug = site.settings;

  return (
    <Staff.View
      title="Commerce"
      className="no-bg"
      back="/staff/"
      actions={
        <div className="d-flex">
          <Staff.NavLink to="notifications/" className="btn btn-round btn-secondary-3" title="Action needed">
            <Icons.Notification />
          </Staff.NavLink>

          <Staff.NavLink to="settings/" className="btn btn-round btn-secondary-3" title="Shop settings">
            <Icons.Adjustments />
          </Staff.NavLink>
        </div>
      }
    >
      <Staff.Section>
        <div className="d-grid grid-2 grid-sm-3 grid-md-4 grid-lg-6">
          <CardLink
            title="Products"
            tab="./?tab=products"
            link={{ to: 'products/', text: 'View all products', perms: ['store.view_product'] }}
          >
            <h3>{products?.count || 0}</h3>
          </CardLink>

          <CardLink
            tab="./?tab=categories"
            title="Categories"
            link={{ to: 'categories/', text: 'View all categories', perms: ['store.view_category'] }}
          >
            <h3>{cats?.count || 0}</h3>
          </CardLink>

          {shopy_settings?.config?.has_suppliers && (
            <CardLink
              title="Supplier Orders"
              tab="./"
              link={{ to: 'orders/', text: 'View all orders', perms: ['store.view_supplierorder'] }}
            >
              <h3>{orders?.count || 0}</h3>
            </CardLink>
          )}
        </div>
      </Staff.Section>

      {settingSlug && <PerformanceCard {...{ settingSlug }} />}

      <Staff.Section title="Produits les plus vus" className="my-2">
        <div className="d-grid grid-5 grid-md-10 gap-2">
          {stats?.prods?.map((p) => {
            return (
              <div key={p.name}>
                <div style={{ position: 'relative' }}>
                  <img
                    src={getImgUrl(p?.cover_data?.thumb_sq)}
                    title={`(${p?.count}) ${p?.name}`}
                    alt={`${p?.name}:${p?.count}`}
                    className="rounded"
                    style={{ objectFit: 'cover', width: '100%', height: 'auto', aspectRatio: '1/1' }}
                  />
                  <div
                    className="d-flex align-items-center justify-content-center"
                    style={{
                      position: 'absolute',
                      bottom: 10,
                      left: 5,
                      padding: '2px 4px',
                      backgroundColor: 'white',
                      borderRadius: 6,
                    }}
                  >
                    {p?.count}
                  </div>
                </div>
                <div className="text-sm text-ellipsis">{p?.name}</div>
              </div>
            );
          })}
        </div>
      </Staff.Section>

      <Staff.Section title="CatÃ©gories les plus vues" className="my-2">
        <div className="d-grid grid-5 grid-md-10 gap-2">
          {stats?.cats?.map((p) => {
            return (
              <div key={p.name}>
                <div style={{ position: 'relative' }}>
                  <img
                    src={getImgUrl(p?.cover_data?.thumb_sq)}
                    title={`(${p?.count}) ${p?.name}`}
                    alt={`${p?.name}:${p?.count}`}
                    className="rounded"
                    style={{ objectFit: 'cover', width: '100%', height: 'auto', aspectRatio: '1/1' }}
                  />
                  <div
                    className="d-flex align-items-center justify-content-center"
                    style={{
                      position: 'absolute',
                      bottom: 10,
                      left: 5,
                      padding: '2px 4px',
                      backgroundColor: 'white',
                      borderRadius: 6,
                    }}
                  >
                    {p?.count}
                  </div>
                </div>
                <div className="text-sm text-ellipsis">{p?.name}</div>
              </div>
            );
          })}
        </div>
      </Staff.Section>

      <Staff.Section title="Nombre de produits par categorie" className="my-2">
        {cats?.cat_count
          ?.filter((i) => i.category__name)
          ?.map(({ category__name, count }, i) => (
            <div key={i}>
              {category__name}: {count}
            </div>
          ))}
      </Staff.Section>
    </Staff.View>
  );
}

const CardLink: React.FC<{
  title: string;
  tab?: string;
  link: { to: string; text: string; perms: string[] };
  children: React.ReactNode;
}> = ({ children, title, link, tab = './' }) => {
  const { to } = link;
  return (
    <div className="card-link mb-3">
      <Staff.NavLink to={tab}>
        {title && <div className="card-link-title">{title}</div>}
        {children}
      </Staff.NavLink>

      <Staff.Link to={to} requiredPerms={link.perms}>
        {link?.text && `${link.text} Â»`}
      </Staff.Link>
    </div>
  );
};
